* {
  margin: 0;
  box-sizing: border-box;
}

body {
  font-family: 'Share Tech Mono', Roboto-Bold;
  min-height: 100vh;
  margin: 0;
  background-color: #f8f0f5; 
  color: #333;
}

::selection {
  background-color: rgb(255, 105, 180); 
  color: rgb(255, 255, 255);
}

::-moz-selection {
  background-color: rgb(255, 105, 180);
  color: rgb(255, 255, 255);
}

::-webkit-scrollbar {
  width: 10px;
  height: 10px;
}

::-webkit-scrollbar-track {
  background: #f1c1d4;  
}

::-webkit-scrollbar-thumb {
  background: rgba(255, 105, 180, 0.7);  
  border-radius: 10px;
}

::-webkit-scrollbar-thumb:hover {
  background: rgb(255, 105, 180);
}

.wrapper {
  min-height: 100vh;
  overflow: hidden;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #f8f0f5;  
}

.title {
  position: absolute;
  top: 60px;
  margin: auto;
  font-size: 2.5rem;
}

#drum-machine {
  padding: 40px;
  box-shadow: 10px 10px 40px rgba(255, 105, 180, 0.5);
  border-radius: 15px;
  background: #ffebee;  
  text-align: center;
}

#display {
  font-size: 30px;
  padding: 10px 20px;
  display: inline-block;
  background-color: #f1c1d4;  
  border: 5px solid rgb(255, 105, 180);  
  min-width: 300px;
  margin-bottom: 10px;
  border-radius: 5px;
}

.drum-pads {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  max-width: 600px;
}

.drum-pad {
  background: #ffccdc;  
  display: flex;
  justify-content: center;
  align-items: center;
  height: 80px;
  width: calc(33.33% - 10px);
  border: 2px solid rgb(255, 105, 180);  
  cursor: pointer;
  margin: 5px;
  transition: background 0.3s, transform 0.1s;  
}

.drum-pad:hover {
  background: rgb(255, 105, 180);  
}

.drum-pad.drum-pad-active {
  background: rgb(255, 182, 193);  
  box-shadow: 0 0 15px rgba(255, 105, 180, 0.8);
  transform: scale(0.9);  
}

.drum-text {
  font-size: 20px;
}

@media only screen and (max-width: 900px) {
  .drum-pad {
      width: calc(33.33% - 10px);
  }

  #display {
      min-width: 100%;
  }
}

@media only screen and (max-width: 400px) {
  #display {
      font-size: 25px;
  }

  .drum-pad {
      height: 60px;
      width: calc(33.33% - 10px);
  }
}
